*{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2c3a47;
}
.digital-clock{
  width: 400px;
  background-color: #ffff;
  padding: 20px;
  border-radius: 4px;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
h1{
   color: orangered;
   font-weight: 600;
    font-size: 60px;
    margin-bottom: 15px;
}
.time{
   margin-bottom: 15px;
   font-size: 50px;
   font-weight: 700;
}
.date{
  color: #888;
  font-size: 20px;
}